.text {
	margin: 0;
	padding: 0;
	transition: color $trans-default;
	font-weight: 400;
	font-size: 20px;
	line-height: 24px;
	&--sm {
		// sm = small
		font-size: 0.9em;
		line-height: 1.1;
	}
	&--md {
		// md = middle
		font-size: 1.3em;
		line-height: 1.3;
	}
	&--lg {
		// lg = large
		font-weight: 500;
		font-size: 1.5em;
	}
}
